    .titulo-tres.mt-5: h3 Etapas del proceso de evaluacin BPG

    p.mt-4 La certificacin en Buenas Prcticas Ganaderas (BPG) para predios lecheros se desarrolla a travs de un proceso estructurado que garantiza el cumplimiento de los requisitos sanitarios, ambientales y de bienestar animal. A continuacin, se describen sus principales etapas:

    .row.mt-4.align-items-center
      .col-lg-7
