          div(titulo="B.	Visita de inspeccin")

            p.px-5 Auditora integral al predio, con aplicacin del Formulario 3-852 v6, que evala componentes clave del sistema.

          div(titulo="C.	Calificacin de hallazgos")

            p.px-5 Los tems se marcan como Cumple (C), No cumple (NC) o No aplica (NA). Los hallazgos se clasifican segn su impacto.

          div(titulo="D.	Informe de auditora")

            p.px-5 El equipo auditor emite un informe tcnico con observaciones, hallazgos y recomendaciones de mejora.

          div(titulo="E.	Certificacin")

            p.px-5 Si no existen hallazgos crticos, se otorga la certificacin. En caso contrario, se establecen plazos para subsanar.

