    .titulo-tres.mt-5: h3 Indicadores de gestin 

    p.mt-4 Los indicadores de gestin son expresiones cuantitativas del comportamiento y desempeo de un proceso. Su magnitud, al compararse con un nivel de referencia, puede sealar desviaciones que ameriten acciones correctivas o preventivas (Da Silva, 2021). En otras palabras, un indicador de gestin es un valor medible que permite evidenciar el nivel de logro de los objetivos clave propuestos.

