    .titulo-tres.mt-5: h3 Pasos para elaborar una diagramacin:

    p.mt-4 La diagramacin de procesos es una herramienta fundamental para representar de manera visual, ordenada y comprensible las actividades tcnicas, sanitarias y operativas que se llevan a cabo en un predio lechero. Su uso en el marco de la evaluacin de Buenas Prcticas Ganaderas (BPG) permite:

    .row.justify-content-center.mt-4
      .col-lg-10(data-aos="fade-down")
        .row.d-flex.align-items-stretch.bg-10
